import React from 'react'

const Contact = () => {
    return (
        <>
            <div className="container vh-100 contact-page d-flex flex-column align-items-center my-5">
                <div className=" fs-3 d-flex justify-content-center ">
                    <header className='page-header p-1 px-3 my-3 mb-4'>Hire Me</header>
                </div>
                <form className="row mt-3 w-75 g-3 " >
                    <div className="col-md-12 border-top-0">

                        <input type="text" className="form-control border-top-0 border-end-0 border-3 border-dark" id="name" placeholder='ENTER YOUR NAME *' />
                    </div>
                    <div className="col-md-12">
                        <input type="email" className="form-control border-top-0 border-end-0 border-3 border-dark" id="email" placeholder='ENTER YOUR EMAIL *' required />
                    </div>
                    <div className="col-md-12">
                        <input type="number" className="form-control border-top-0 border-end-0 border-3 border-dark" id="number" placeholder='PHONE NUMBER *' required />
                    </div>
                    <div className="col-md-12">
                        <textarea name="textarea" className='form-control border-top-0 border-end-0 border-3 border-dark' id="textarea" placeholder='YOUR MESSAGE' cols="20" rows="6"></textarea>
                    </div>
                    <div className="col-md-3 ">
                        <button type="submit" className="form-control btn btn-primary">Submit</button>
                    </div>
                </form>
            </div>
        </>
    )
}

export default Contact
